#c-all{
    width: 600px;
    padding: 0 0 120px 0 ;
    .content{
        width: 100%;
        position: relative;
        .input{
            display: flex;
            position: relative;
            padding: 0 0 80px 0;
            img{
                position: absolute;
                top: 13px;
                left: 15px;
                width: 18px;
                height: 18px;
            }   
            input{
                width: 100%;
                height: 44px;
                background-color: #f4f4f4;
                padding: 8px 16px 8px 40px;
                font-size: 16px;
                background-color: rgb(244, 244, 244);
                border-radius: 8px;
                border: 0;
                &::placeholder{
                    color: rgb(185, 185, 185);
                }
            }
        }
        .slide-box{
            margin-bottom: 50px;
            position: relative;
            .pp{
                display: flex;
                align-items: center;
                justify-content: space-between;
                padding:  0 0 15px 0;
                > strong{
                    font-size: 18px;
                    line-height: 28px;
                    font-weight: 700;
                    color: rgb(45, 45, 45);
                }   
                span{
                    font-size: 12px;
                    color: #888;
                    em{}
                    i{}
                }
               
            }
          
            .slide-container{
                width: 100%;
                position: relative;
               .slide-view{
                width: 100%;
                position: relative;
                overflow: hidden;
                .slide-wrap{
                    width: 33.333% * 6;
                    display: flex;
                    position: relative;
                    .slide{
                        width:190px;
                        height: 180px;
                        margin: 0 5px;
                        padding: 20px;
                        background-color:rgb(244, 244, 244);
                        color: rgb(50, 50, 50);
                        border-radius: 16px;
                        position: relative;
                        > a{
                            .slide-item{
                         
                                > h3{
                                    color: rgb(136, 136, 136);
                                    font-size: 12px;
                                    font-weight: 500;
                                    padding: 0 0 6px 0;
                                }
                                > p{
                                    margin-bottom: 24px;
                                    height: 72px;
                                    font-size: 16px;
                                    font-weight: 700;
                                    color: rgb(45, 45, 45);
                                }
                                .p-wrap{
                                    display: flex;
                                    position: relative;
                                    margin: 0 15px 0 0;
                                   .feed-p1{
                                    font-size: 12px;
                                    color: rgb(197, 197, 197);
                                    font-weight: 400;
                                    padding: 0 20px 0 20px ;
                                    position: relative;
                                    margin: 0 10px 0 0;
                                    &::before{
                                        content: '';
                                        width: 20px;
                                        height: 16px;
                                        background: url(../../../../../../public/images/community/content/icon-view.svg) no-repeat 50% 50%;
                                        position: absolute;
                                        top: 2px;
                                        left: 0;
                                        z-index: 3;
                                    }
                                   }
                                   .feed-p2{
                                    font-size: 12px;
                                    color: rgb(197, 197, 197);
                                    font-weight: 400;
                                    &::before{
                                        content: '';
                                        width: 20px;
                                        height: 16px;
                                        background: url(../../../../../../public/images/community/content/icon-list.svg) no-repeat 50% 50%;
                                        position: absolute;
                                        top: 2px;
                                        left: 50px;
                                        z-index: 3;
                                    }
                                   }
                                }
                            }
                        }

                    }
                    .slide1{
                        width:194px;
                        height: 180px;
                        margin: 0 5px;
                        padding: 20px;
                        background-color: rgb(0, 199, 174);
                        border-radius: 16px;
                        > a{
                            .slide-item{
                         
                                > h3{
                                    color: #fff;
                                    font-size: 12px;
                                    font-weight: 500;
                                    padding: 0 0 6px 0;
                                }
                                > p{
                                    margin-bottom: 24px;
                                    height: 72px;
                                    font-size: 16px;
                                    font-weight: 700;
                                    color: #fff;
                                }
                                .p-wrap{
                                   > p{
                                    font-size: 12px;
                                    color: #fff;
                                    font-weight: 400;
                                   }
                                }
                            }
                        }

                    }
                }
               } 
            }
            .arrow-btn-next{
                position: absolute;
                top: 50%;
                right: -20px;
                &.on{
                    display: none;

                }
                
                img{
                    width: auto;
                }   
            }
            .arrow-btn-prev{
                position: absolute;
                top: 50%;
                left: -20px;
                display: none;
                &.on{
                    display: block;

                }
                img{
                    width: auto;

                }   
            }
        }
        .btn-wrap{
            width: 100%;
            height: 54px;
            padding: 8px;
            button{
                height: 38px;
                padding: 8px 16px;
            }
          .service{
            height: 38px;
            padding: 8px 16px;
            margin: 0 8px 0 0;
            width: 92.64px;
            border-radius: 19px;
            background-color: #fff;
            color: rgb(45, 45, 45);
            border: 1px solid rgb(225, 225, 225);
            span{
                font-size: 14px;
            }
            img{
                width: 16px;
                height: 16px;
                margin-bottom:5px ;
            }
           
          }   
          .location{
            height: 38px;
            padding: 8px 16px;
            margin: 0 8px 0 0;
            width: 80.64px;
            border-radius: 19px;
            background-color: #fff;
            color: rgb(45, 45, 45);
            border: 1px solid rgb(225, 225, 225);
            span{
                font-size: 14px;
            }
            img{
                width: 16px;
                height: 16px;
                margin-bottom:5px ;
            }
          }   
          .blind{
            display: none;
          }
        }
        .feed-list{
            width: 600px;
            //height: 194.91px;
            .feed{
                width: 100%;
                padding: 20px 8px;
                border-bottom:1px solid rgb(244, 244, 244) ;    
                > a{
                    width: 100%;
                    .wrap-wrap{
                        display: flex;
                        align-items: center;
                        justify-content: flex-start;
                        .text-wrap{
                            width: 100%;
                          
                            margin: 0 5px 0 0;
                            .feed-content{
                             
                              
                                h4{
                                    font-size: 12px;
                                    color: rgb(136, 136, 136);
                                    line-height: 18px;
                                }
                                h3{
                                    font-size: 14px;
                                    color: rgb(45, 45, 45);
                                    list-style: 22px;
                                    font-weight: 500;
                                    margin-bottom: 4px;
                                }
                                .c{
                                    text-align: left;
                                    color: rgb(136, 136, 136);
                                    font-size: 14px;
                                    line-height: 22px;
                                    letter-spacing: -0.3px;
                                    margin-bottom: 10px;
                                }
                                .l{
                                    color: rgb(136, 136, 136);
                                    font-size: 12px;
                                    line-height: 18px;
                                    letter-spacing: -0.3px;
                                    margin-bottom: 5px;
    
                                }
                                .user_id{
                                    color: rgb(136, 136, 136);
                                    font-size: 12px;
                                    line-height: 18px;
                                    letter-spacing: -0.3px;
                                    margin-bottom: 10px;
                                }
                            }
    
                        }
                        .img-wrap{
                     
                            img{
                                border: 1px solid rgb(244, 244, 244);
                                border-radius: 8px;
                            }
                        }
                    }

                    .feed-footer{
                        display: flex;
                        align-items: center;
                        justify-content: space-between;
                        .span-wrap{
                            position: relative;
                            .span1{
                                color: rgb(197, 197, 197);
                                font-size: 12px;
                                line-height: 18px;
                                letter-spacing: -0.2px;
                                padding: 0 30px 0 20px;
                                &::before{
                                    content: '';
                                    width: 20px;
                                    height: 16px;
                                    background: url(../../../../../../public/images/community/content/icon-thumb.svg) no-repeat 50% 50%;
                                    position: absolute;
                                    top: 3px;
                                    left:0 ;
                                    z-index: 3;
                                }
                            }
                            .span2{
                                color: rgb(197, 197, 197);
                                font-size: 12px;
                                line-height: 18px;
                                letter-spacing: -0.2px;
                                &::before{
                                    content: '';
                                    width: 20px;
                                    height: 16px;
                                    background: url(../../../../../../public/images/community/content/icon-list.svg) no-repeat 50% 50%;
                                    position: absolute;
                                    top: 4px;
                                    left: 35px;
                                    z-index: 3;
                                }
                            }
                        }

                        .span3{
                            color: rgb(197, 197, 197);
                            font-size: 12px;
                            line-height: 18px;
                            letter-spacing: -0.2px;
                        }
                    }

                }
            }
        }
    }
}